<template>
  <div class="swiper-box">
    <!-- {{list}} -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div
          v-for="(item,index) in list"
          :key="index"
          class="swiper-slide"
          :style="`background-image:url(${item.img})`"
        ></div>
        <!-- <div
        class="swiper-slide"
         style="background:red"
      ></div>
      <div
        class="swiper-slide"
        style="background:pink"
      ></div>
      <div
        class="swiper-slide"
         style="background:orange"
      ></div>
      <div
        class="swiper-slide"
        style="background:green"
        ></div>-->
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import mySwiper from "swiper";
export default {
  created() {
    if (this.list.length) return;
    // vuex bannerList 
    this.$store.dispatch("getBanner");
  },
  mounted() {
    //   dom 
   this.init();
  },
  // vuexcomputed
  computed: {
    list() {
      return this.$store.state.bannerList;
    }
  },
  methods: {
    // 
    init() {
      new mySwiper(".swiper-container", {
        loop: true, // 
        effect: "cube",
        grabCursor: true,
        cubeEffect: {
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94
        },
        pagination: {
          el: ".swiper-pagination"
        }
      });
    }
  },
  updated() {
    // new 
    // 
    console.log(this.list);
    // mountedthis.list
    // swiper-slide swiper
    this.init();
  }
};
</script>

<style lang="less" scoped>
.swiper-box {
  background-color: #ccc;
  position: relative;
  height: 4rem;
  top: 1rem;
  overflow: hidden;
}
.swiper-container {
  width: 4rem;
  height: 4rem;
  position: absolute;
  left: 25%;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
}
</style>
